import React, {Component} from 'react';
import Cut from "../../Cut/Cut";
import {Tabs} from 'antd';

const {TabPane} = Tabs;

class Work extends Component {
    constructor(props) {
        super(props);
        this.state = {
            workIsShow: true,
            current: 'mail',
        };
    }

    render() {
        const {workIsShow} = this.state;
        return (
            <div>
                <Cut _showAndHidden={() => this._workShowAndHidden()} text="工作经历"/>
                <Tabs className={(workIsShow ? "show " : "hidden ") + "myAnimate"} defaultActiveKey="1">
                    <TabPane tab="成都精湛职业学校" key="1">
                        <h1>成都精湛职业学校</h1>
                        <p>市场运营 全职 2019.02 - 2019.12</p>
                        <ol className="olList">
                            <li>
                                <h3>1、成人自考招生：中专大专本科文凭提供给有需要的在职学生或者已经步入社会的工作人士，提升自己的市场竞争力。</h3>
                            </li>
                            <li>
                                <h3>2、资料制作：用Word及各种办公软件制作工作文件，如公司资质文件，公司招生文件，公司业务类别文件等。</h3>
                            </li>
                            <li>
                                <h3>3、数据汇总：一些手动需要费时费力的事情全部自行编写Excel宏进行简单的自动化操作，将工作的效率提高数十倍。</h3>
                            </li>
                            <li>
                                <h3>4、大量处理证件照：通过PS将不符合规则的证件照进行裁剪，去掉除白色以外的所有背景。</h3>
                            </li>
                            <li>
                                <h3>5、技术工人鉴定：讲解考试规则，维持考场纪律，考生不得交头接耳、左顾右盼、打手势，如果出现以上情况进行警告处理。</h3>
                            </li>
                        </ol>
                    </TabPane>
                    <TabPane tab="完美世界(北京)网络技术有限公司" key="2">
                        <h1>完美世界(北京)网络技术有限公司</h1>
                        <p>游戏内违规处理/IM 全职 2016.06 - 2018.04</p>
                        <ol  className="olList">
                            <li>
                                <h3>1、在线客服：进行最多达到1对5的接线，回复不得超过5分钟，满意率不得低于80%。</h3>
                            </li>
                            <li>
                                <h3>2、违规审核：对玩家在游戏中的发言进行审核，判断是否有违规情况，如果有违规则根据情况进行不同的处理。</h3>
                            </li>
                            <li>
                                <h3>3、论坛管理：对论坛中的头像，以及在论坛中发布的内容，论坛的角色名称是否有违规进行审核，如果发现有违规，则进行删除。</h3>
                            </li>
                            <li>
                                <h3>4、统计数据：每天早上将头天的数据进行分类汇总整理后发送给值班，然后由值班制作头天的日报。</h3>
                            </li>
                        </ol>
                    </TabPane>
                    <TabPane tab="成都市彼索科技有限公司" key="3">
                        <h1>成都市彼索科技有限公司</h1>
                        <p>游戏在线客服 全职 2015.11 - 2016.04</p>
                        <ol  className="olList">
                            <li>
                                <h3>1、新人引导：引导游戏中新人玩家，促进玩家对游戏的热情，定期举行活动。</h3>
                            </li>
                            <li>
                                <h3>2、在线客服：回答玩家问题，同时将玩家反馈的内容进行分类收集。</h3>
                            </li>
                            <li>
                                <h3>3、游戏测试：帮助策划测试游戏，提出一些简单的建议。</h3>
                            </li>
                        </ol>

                    </TabPane>
                </Tabs>
            </div>
        );
    }

    _workShowAndHidden() {
        let {workIsShow} = this.state;
        workIsShow = !workIsShow;
        this.setState({
            workIsShow
        });
    }
}

export default Work;